body {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 14px;
  line-height: 24px;
  color: #ffffff;
  background: #000000;
}
a {
  text-decoration: none;
  transition: all 0.25s ease-in-out;
}
img {
  vertical-align: middle;
  max-width: 100%;
  height: auto;
}
.hidden {
  display: none !important;
}
.item-demo-iframe {
  display: block;
  max-width: 100%;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  border: 0;
  padding-top: 50px;
  transition: all .3s;
  background: #ffffff;
}
.bottom-switcher .item-demo-iframe {
  padding-top: 0;
  padding-bottom: 50px;
}
.switcher-bar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 50px;
  overflow: hidden;
  background: #232323;
  border-bottom: 1px solid #000000;
}
.switcher-bar:before,
.switcher-bar:after {
  content: " ";
  display: table;
}
.switcher-bar:after {
  clear: both;
}
.bottom-switcher .switcher-bar {
  top: auto;
  bottom: 0;
  border-bottom: 0;
  border-top: 1px solid #000000;
}
.switcher-bar .logo {
  display: block;
  float: left;
  color: inherit;
  padding: 0 20px;
  border-right: 1px solid #363636;
  line-height: 50px;
  font-weight: 600;
  font-size: 18px;
  letter-spacing: 1px;
}
.switcher-bar .logo img {
  display: block;
  width: auto;
  height: 30px;
  margin-top: 10px;
  margin-bottom: 10px;
  transition: all 0.25s ease-in-out;
}
.switcher-bar .logo:hover {
  color: #82b440;
}
.switcher-bar .logo:hover img {
  opacity: .75;
}
@media (max-width: 767px) {
  .switcher-bar .logo {
    border-right: 0;
  }
}
.switcher-bar .remove-frame-btn {
  float: right;
  color: #d9d9d9;
  line-height: 50px;
  border-left: 1px solid #363636;
  padding: 0 20px;
  font-size: 12px;
}
.switcher-bar .remove-frame-btn:hover {
  color: #ffffff;
}
.switcher-bar .remove-frame-btn .fa {
  font-size: 14px;
  color: #ffffff;
  margin-right: 15px;
}
@media (max-width: 991px) {
  .switcher-bar .remove-frame-btn span {
    display: none;
  }
  .switcher-bar .remove-frame-btn .fa {
    margin-right: 0;
  }
}
.switcher-bar .buy-now {
  float: right;
  border-left: 1px solid #363636;
  padding: 10px 20px;
}
.switcher-bar .buy-now .buy-now-btn {
  display: block;
  height: 30px;
  background-color: #82b440;
  border-bottom: 2px solid #74a139;
  border-radius: 3px;
  line-height: 20px;
  padding: 5px 15px 3px;
  color: #ffffff;
}
.switcher-bar .buy-now .buy-now-btn:hover {
  background-color: #74a139;
  border-bottom: 2px solid #678e33;
}
.switcher-bar .buy-now .buy-now-btn .fa {
  margin-right: 10px;
}
@media (max-width: 767px) {
  .switcher-bar .buy-now {
    border-left: 0;
  }
}
.switcher-bar .viewports {
  float: right;
  list-style: none;
  padding: 0 20px;
  margin: 0;
}
.switcher-bar .viewports li {
  float: left;
  margin-right: 20px;
}
.switcher-bar .viewports li:last-child {
  margin-right: 0;
}
.switcher-bar .viewports li a {
  display: block;
  line-height: 50px;
  color: #d9d9d9;
  font-size: 18px;
  padding: 0 5px;
}
.switcher-bar .viewports li a:hover {
  color: #ffffff;
}
.switcher-bar .viewports li a .fa {
  position: relative;
  top: 2px;
}
.switcher-bar .viewports li.active a {
  color: #82b440;
  cursor: default;
}
@media (max-width: 767px) {
  .switcher-bar .viewports {
    display: none;
  }
}
.switcher-bar .items-toggle-btn {
  float: left;
  padding: 1px 20px 0;
  line-height: 50px;
  color: #ffffff;
  font-weight: 500;
}
.switcher-bar .items-toggle-btn span {
  position: relative;
  top: -1px;
  background: #82b440;
  border: 1px solid #74a139;
  border-radius: 3px;
  padding: 2px 5px;
  margin-left: 10px;
  font-size: 10px;
  text-transform: uppercase;
  color: #ffffff;
  transition: all 0.25s ease-in-out;
}
.switcher-bar .items-toggle-btn:after {
  position: relative;
  top: -1px;
  font-family: 'FontAwesome';
  font-size: 12px;
  content: '\f078';
  margin-left: 15px;
}
.switcher-bar .items-toggle-btn:hover {
  color: #82b440;
}
.bottom-switcher .switcher-bar .items-toggle-btn:after {
  content: '\f077';
}
@media (max-width: 767px) {
  .switcher-bar .items-toggle-btn {
    display: none;
  }
}
.owl-carousel {
  display: none;
  position: relative;
  width: 100%;
  touch-action: pan-y;
}
.owl-carousel .owl-wrapper {
  display: none;
  position: relative;
  transform: translate3d(0px, 0px, 0px);
}
.owl-carousel .owl-wrapper:before,
.owl-carousel .owl-wrapper:after {
  content: " ";
  display: table;
}
.owl-carousel .owl-wrapper:after {
  clear: both;
}
.owl-carousel .owl-wrapper-outer {
  overflow: hidden;
  position: relative;
  width: 100%;
}
.owl-carousel .owl-wrapper-outer.autoHeight {
  transition: height 0.5s ease-in-out;
}
.owl-carousel .owl-item {
  float: left;
}
.owl-carousel .owl-controls {
  user-select: none;
}
.owl-carousel .owl-wrapper,
.owl-carousel .owl-item {
  backface-visibility: hidden;
  transform: translate3d(0, 0, 0);
}
.switcher-carousel {
  position: fixed;
  top: 50px;
  left: 0;
  width: 100%;
  overflow: hidden;
  background: #3d3d3d;
  border-bottom: 1px solid #232323;
  z-index: 999990;
  padding: 30px 60px;
  visibility: hidden;
  opacity: 0;
  transition: all 0.25s ease-in-out;
}
.bottom-switcher .switcher-carousel {
  top: auto;
  bottom: 50px;
  border-bottom: 0;
  border-top: 1px solid #232323;
}
.switcher-carousel.active {
  visibility: visible;
  opacity: 1;
}
.switcher-carousel .owl-prev,
.switcher-carousel .owl-next {
  position: absolute;
  display: block;
  cursor: pointer;
  top: 50%;
  width: 60px;
  line-height: 60px;
  font-size: 28px;
  text-align: center;
  color: #ffffff;
  margin-top: -30px;
  z-index: 999999;
  transition: all 0.25s ease-in-out;
}
.switcher-carousel .owl-prev:hover,
.switcher-carousel .owl-next:hover {
  color: #82b440;
}
.switcher-carousel .owl-prev.disabled,
.switcher-carousel .owl-next.disabled {
  cursor: default;
  opacity: .5;
  color: #ffffff;
}
.switcher-carousel .owl-prev {
  left: -60px;
}
.switcher-carousel .owl-next {
  right: -60px;
}
.switcher-carousel .carousel-wrapper {
  position: relative;
  padding: 0;
  margin: 0;
}
.switcher-carousel .carousel-wrapper:before,
.switcher-carousel .carousel-wrapper:after {
  content: " ";
  display: table;
}
.switcher-carousel .carousel-wrapper:after {
  clear: both;
}
.switcher-carousel .carousel-wrapper .item {
  position: relative;
  width: 250px;
  height: 158px;
  display: block;
  border: 1px solid #232323;
  border-radius: 3px;
  cursor: pointer;
  margin: 0 auto;
}
.switcher-carousel .carousel-wrapper .item img {
  display: block;
  border-radius: 3px 3px 0 0;
}
.switcher-carousel .carousel-wrapper .item .title {
  display: block;
  background: #ffffff;
  color: #232323;
  width: 100%;
  overflow: hidden;
  padding: 3px 15px;
  text-align: center;
  text-overflow: ellipsis;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 500;
  letter-spacing: 1px;
  white-space: nowrap;
  border-radius: 0 0 3px 3px;
}
.switcher-carousel .carousel-wrapper .item .badge {
  position: absolute;
  display: block;
  top: 5px;
  right: 5px;
  background: #82b440;
  border: 1px solid #74a139;
  border-radius: 3px;
  padding: 0 5px;
  font-size: 10px;
  color: #ffffff;
  text-transform: uppercase;
  transition: all 0.25s ease-in-out;
}
@media (max-width: 767px) {
  .switcher-carousel {
    display: none;
  }
}
